/* General Styling */
body {
  background-color: #121212;
  color: #E0E0E0;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: hidden;
}

.container {
  background-color: #1E1E1E;
  padding: 30px 30px 90px; /* Combined padding */
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  text-align: center;
  max-height: 80vh;
  overflow-y: auto;
  max-width: 800px;
  width: 80%;
  margin: 0 auto;
  position: relative;
  min-width: 300px;
}

h2 {
  font-size: 24px;
  color: #FFFFFF;
  margin-bottom: 20px;
}

.input-group {
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

input[type="text"], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border: 2px solid #757575;
  border-radius: 4px;
  background-color: #2E2E2E;
  color: #E0E0E0;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

input[type="text"]:focus, select:focus {
  border-color: #64FFDA;
  outline: none;
}

label {
  display: inline-block;
  width: auto;
  font-size: 16px;
  color: #B0BEC5;
  flex-grow: 1;
  text-align: left; 
}

button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #008080;
  color: #FFFFFF;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #006666;
}

button:disabled {
  background-color: #A9A9A9;
  cursor: not-allowed;
}

.characteristics-group {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.characteristics-group span {
  font-size: 1.5rem;
}

.characteristics-group .char-button {
  font-size: 0.75rem;
  padding: 5px;
  width: 30px;
  height: 30px;
  background-color: #008080;
  color: #FFFFFF;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background-color 0.3s;
  position: relative;
  top: -10px;
}

.characteristics-group .char-button:disabled {
  background-color: #A9A9A9;
  cursor: not-allowed;
}

.characteristics-group .char-button:hover:enabled {
  background-color: #006666;
}

.points-remaining {
  margin: 40px 0;
  font-weight: bold;
  color: #E0E0E0;
  font-size: 1.5rem;
}

.tooltip {
  position: relative;
  display: inline-block;
}

.spell-button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #2E2E2E;
  color: #E0E0E0;
  border: 1px solid #757575;
  border-radius: 5px;
  margin: 10px 0;
  cursor: pointer;
  transition: background-color 0.3s;
  max-width: 300px;
  word-wrap: break-word;
  white-space: normal;
}

.spell-button.selected {
  background-color: #008080;
}

.spell-button:hover {
  background-color: #3C3C3C;
}

.spell-button.selected:hover {
  background-color: #008080;
}

@media (max-width: 768px) {
  .container {
    padding: 10px;
    width: 95%;
    margin: 0 auto 20px;
    min-width: 90%; /* Adjusting the minimum width */
  }

  .input-group {
    flex-direction: column;
    margin-bottom: 15px;
  }

  .description {
    margin-left: 10px;
    font-size: 1em;
  }

  h2, h3, h4 {
    font-size: 1.2em;
  }

  label {
    font-size: 1em;
  }

  .button-group {
    display: flex;
    flex-direction: column;
  }

  .button-group button {
    margin-bottom: 10px;
  }
}
